const StackBlur = require("stackblur-canvas");
const Sobel = require("sobel");
const spiral_order = require("./spiral_order.js")
var saveAs = require("file-saver");
class wc_generator {
    constructor(my_canvas) {
        // link the canvas
        this.canvas = my_canvas;
        this.height = my_canvas.height;
        this.width = my_canvas.width;
        // how many pixels occupied
        this.occupied = 0;
        // spiral order in a 25 by 25 grid
        this.spiral_order = new spiral_order().order;
        // set initial null for sobel filter
        this.sob = null;
    }

    loadImage(image_src, blurAmount) {
        var img = new Image();
        img.onload = () => {
            this.canvas.height = img.height;
            this.canvas.width = img.width;
            this.height = img.height;
            this.width = img.width;
            const ctx = this.canvas.getContext("2d");
            ctx.drawImage(img, 0, 0);
            this.raw_image_data = ctx.getImageData(0, 0, this.width, this.height);
            StackBlur.canvasRGB(
                this.canvas,
                0,
                0,
                this.width,
                this.height,
                blurAmount
            );
        };
        img.src = image_src;
        // the sob filter should be reset
        this.sob = null;
    }

    blurImage(blurAmount) {
        this.canvas.getContext("2d").putImageData(this.raw_image_data, 0, 0);
        StackBlur.canvasRGB(
            this.canvas,
            0,
            0,
            this.width,
            this.height,
            blurAmount
        );
        // reset sob filter
        this.sob = null;
    }

    showImage() {
        this.canvas.getContext("2d").putImageData(this.raw_image_data, 0, 0);
    }

    sobFilter(blurAmount, accuracy) {
        const accuracy_percent = accuracy / 100;
        const ctx = this.canvas.getContext("2d");
        ctx.putImageData(this.raw_image_data, 0, 0);
        StackBlur.canvasRGB(
            this.canvas,
            0,
            0,
            this.width,
            this.height,
            blurAmount
        );
        this.sob = Sobel(ctx.getImageData(0, 0, this.width, this.height));
        for (var i = 0; i < this.sob.length; i += 4) {
            const bar = this.sob[i];
            if (bar > (1 - accuracy_percent) * 255 && Math.random() * 255 < accuracy_percent * bar) {
                ;
            } else {
                this.sob[i + 3] = 0
            }
        }
        ctx.putImageData(this.sob.toImageData(), 0, 0);
    }

    generateTxtInfo(selected_words, font_size_base) {
        // create a temp canvas just to draw the words
        var tmp_canvas = document.createElement('canvas');
        tmp_canvas.width = this.width;
        tmp_canvas.height = this.height;
        var ctx = tmp_canvas.getContext('2d');
        // store the word pixel info including size and where the pixels lies
        this.word_infos = [];
        var i = 0;
        var j = 0;
        var largest_font_size = this.width; // we shall go beyond this font size, too large
        // for each words, we will draw at most 9 font sizes
        this.word_largest_font_reached = []
        for (i; i < selected_words.length; i++) {
            this.word_infos.push([]);
            if (i < 50) {
                for (j = i > Math.min(selected_words.length * 0.8, 40) ? -1 : 0; j < font_size_base.length; j++) {
                    // determine the font size for this text, the larger the better, but always have a smallest font to fill the gaps
                    const font_size = j == -1 ? Math.ceil((50 - i) / 5 + 5) : Math.ceil(Math.log10(Math.max(this.width, this.height)) * (font_size_base[j] + Math.floor((Math.min(selected_words.length, 50) / (Math.pow(2, i)))) + Math.log2(Math.min(selected_words.length, 50) - i)));
                    if (font_size > largest_font_size) {
                        break;
                    }
                    ctx.clearRect(0, 0, this.width, this.height);
                    ctx.font = font_size + 'px "Recursive", sans-serif';
                    ctx.textBaseline = "top";
                    // measure the text width and height
                    const measure = ctx.measureText(selected_words[i]);
                    const textHeight = Math.ceil(Math.abs(measure.actualBoundingBoxDescent - measure.actualBoundingBoxAscent));
                    const textWidth = Math.ceil(Math.abs(measure.actualBoundingBoxRight - measure.actualBoundingBoxLeft));
                    // if it is too large for the canvas to hold, drop it and any font that is larger
                    if (textHeight >= this.height || textWidth >= this.width) {
                        break;
                    }
                    ctx.fillText(selected_words[i], 0, 0);
                    // get the pixel position
                    const data = ctx.getImageData(0, 0, textWidth, textHeight).data;
                    var occupiedPosition = [];
                    for (var k = 0; k < textHeight; k++) {
                        for (var l = 0; l < textWidth; l++) {
                            const ind = k * textWidth + l;
                            if (data[ind * 4 + 3] != 0) {
                                occupiedPosition.push({ x: l, y: k });
                            }
                        }
                    }
                    const txt_info = { width: textWidth, height: textHeight, txt: selected_words[i], relative_pos: occupiedPosition, font_size: font_size };
                    this.word_infos[i].push(txt_info);
                    // console.log(selected_words[i], font_size);
                }
                largest_font_size = this.word_infos[i].length > 1 ? this.word_infos[i][this.word_infos[i].length - 1].font_size : largest_font_size;
            } else {
                for (j = 0; j < this.word_infos[49].length; j++) {
                    const font_size = this.word_infos[49][j].font_size;
                    ctx.clearRect(0, 0, this.width, this.height);
                    ctx.font = font_size + 'px "Recursive", sans-serif';
                    ctx.textBaseline = "top";
                    // measure the text width and height
                    const measure = ctx.measureText(selected_words[i]);
                    const textHeight = Math.ceil(Math.abs(measure.actualBoundingBoxDescent - measure.actualBoundingBoxAscent));
                    const textWidth = Math.ceil(Math.abs(measure.actualBoundingBoxRight - measure.actualBoundingBoxLeft));
                    // if it is too large for the canvas to hold, drop it and any font that is larger
                    if (textHeight >= this.height || textWidth >= this.width) {
                        break;
                    }
                    ctx.fillText(selected_words[i], 0, 0);
                    // get the pixel position
                    const data = ctx.getImageData(0, 0, textWidth, textHeight).data;
                    occupiedPosition = [];
                    for (k = 0; k < textHeight; k++) {
                        for (l = 0; l < textWidth; l++) {
                            const ind = k * textWidth + l;
                            if (data[ind * 4 + 3] != 0) {
                                occupiedPosition.push({ x: l, y: k });
                            }
                        }
                    }
                    const txt_info = { width: textWidth, height: textHeight, txt: selected_words[i], relative_pos: occupiedPosition, font_size: font_size };
                    this.word_infos[i].push(txt_info);
                }
            }
        }
        for (i = 0; i < this.word_infos.length; i++) {
            // this.word_infos[i] = this.word_infos[i].reverse();
            this.word_infos[i].sort((a, b) => { return b.font_size - a.font_size; });
            this.word_largest_font_reached.push(0);
        }
        // console.log(this.word_infos);
    }

    prepareWC(blur, accuracy) {
        // double insurance
        this.canvas.width = this.width;
        this.canvas.height = this.height;
        // we need to make sure the sobel filter is set
        if (this.sob == null) {
            this.sobFilter(blur, accuracy);
        }

        // clear the canvas, ready for words
        this.canvas.getContext("2d").clearRect(0, 0, this.width, this.height)

        // how much we cannot touch;
        this.filled = 0;

        // push in the available positions based on the result of sob
        this.available_position = [];
        for (var i = 0; i < this.width * this.height; i++) {
            if (this.sob[i * 4 + 3] != 0 || (this.raw_image_data.data[i * 4 + 3] == 0)) {
                // if there's a pixel here, position not available
                this.available_position.push(false);
                this.filled += 1;
            } else {
                // else it is available
                this.available_position.push(true);
            }
        }



        // where is the top left corner of each 25 by 25 grid
        var grid_start_positions = [];
        for (i = 0; i < this.height; i += 25) {
            for (var j = 0; j < this.width; j += 25) {
                grid_start_positions.push({ x: j, y: i });
            }
        }

        // which text shall we pick now
        this.pick_order = 0;

        // shuffle how each word access the 25 by 25 grid
        function shuffle(array) {
            var currentIndex = array.length, temporaryValue, randomIndex;
            var copy_array = [];
            for (var i = 0; i < array.length; i++) {
                copy_array.push(array[i]);
            }

            // While there remain elements to shuffle...
            while (0 !== currentIndex) {

                // Pick a remaining element...
                randomIndex = Math.floor(Math.random() * currentIndex);
                currentIndex -= 1;

                // And swap it with the current element.
                temporaryValue = copy_array[currentIndex];
                copy_array[currentIndex] = copy_array[randomIndex];
                copy_array[randomIndex] = temporaryValue;
            }

            return copy_array;
        }

        this.grid_access_order = []
        for (i = 0; i < this.word_infos.length; i++) {
            grid_start_positions = shuffle(grid_start_positions);
            this.grid_access_order.push(grid_start_positions);
        }

        // for each word, for each grid, what position is it at now
        this.word_grid_access_pos = [];
        for (i = 0; i < this.word_infos.length; i++) {
            var tmp = []
            for (j = 0; j < grid_start_positions.length; j++) {
                tmp.push(0);
            }
            this.word_grid_access_pos.push(tmp);
        }

        // for each word, which grid to access now
        this.word_grid_index = [];
        for (i = 0; i < this.word_infos.length; i++) {
            this.word_grid_index.push(0);
        }

        this.occupied = this.filled; // occupied pixels, do not count as filled

    }

    binarySearchFont(vertical, negative_rotation, picked_text_info, absolute_x, absolute_y) {
        // do binary search for the fittest font size
        var start = 0;
        var end = picked_text_info.length - 1;
        var ans = -1;
        // how wide can we go
        var allowed_span = vertical ? this.height : this.width;
        var fit_in = false;
        while (start <= end) {
            const mid = Math.floor((start + end) / 2);
            const pt = picked_text_info[mid];
            if (vertical) {
                if (negative_rotation) {
                    fit_in = pt.width < allowed_span && absolute_y + pt.width < this.height && absolute_x - pt.height >= 0;
                } else {
                    fit_in = pt.width < allowed_span && absolute_y - pt.width >= 0 && absolute_x + pt.height < this.width;
                }
            } else {
                fit_in = pt.width < allowed_span && absolute_x + pt.width < this.width && absolute_y + pt.height < this.height
            }
            if (fit_in) {
                const relative_pixel_pos = pt.relative_pos;
                var available = true;
                for (var k = 0; k < relative_pixel_pos.length; k++) {
                    var x, y = 0;
                    if (vertical) {
                        if (negative_rotation) {
                            x = absolute_x - relative_pixel_pos[k].y;
                            y = absolute_y + relative_pixel_pos[k].x;
                        } else {
                            x = absolute_x + relative_pixel_pos[k].y;
                            y = absolute_y - relative_pixel_pos[k].x;
                        }
                    } else {
                        x = absolute_x + relative_pixel_pos[k].x;
                        y = absolute_y + relative_pixel_pos[k].y;
                    }
                    const index = y * this.width + x;
                    // if one of the pixel position is not available
                    if (this.available_position[index] != true) {
                        // this size is too large, move the start
                        available = false;
                        allowed_span = relative_pixel_pos[k].x - 1;
                        start = mid + 1;
                        break;
                    }
                }
                if (available) {
                    end = mid - 1;
                    ans = mid;
                }
            } else {
                // it is too large, move the start
                start = mid + 1;
            }
        }
        return ans;
    }

    pickWord(vertical_rate = 0.0) {
        var ctx = this.canvas.getContext("2d");
        // what is the word we pick now
        const pick_index = this.pick_order;
        const picked_text_info = this.word_infos[pick_index];
        // increment the pick order so next time we will put the next word
        this.pick_order = (this.pick_order + 1) % this.word_infos.length;
        // if there is no available font, skip
        if (picked_text_info.length == 0) {
            return (this.filled - this.occupied) / (this.width * this.height - this.occupied);
        }
        // wheather we should do a vertical text
        var vertical = Math.random() <= vertical_rate;
        // if true, then -90 degrees, else, 90 degrees
        var negative_rotation = Math.random() <= 0.5;
        var vertical_positive_fitin = false;
        var vertical_negative_fitin = false;
        var horizontal_fitin = false;
        var fit_in = false;
        // variable declarations
        var x, y = 0;
        var ans = -1;
        // we will at most do one entire sweep
        for (var i = 0; i < this.word_grid_access_pos[pick_index].length; i++) {
            const grid_index = this.word_grid_index[pick_index];
            // get the start position of the grid for this word
            const start_pos = this.grid_access_order[pick_index][grid_index];
            // increment the grid index for this word so next time we will move to the next grid
            this.word_grid_index[pick_index] = (grid_index + 1) % this.word_grid_access_pos[pick_index].length;
            // there are only 625 positions in each grid
            for (var j = this.word_grid_access_pos[pick_index][grid_index]; j < 625; j++) {
                // always move along the spiral
                this.word_grid_access_pos[pick_index][grid_index] += 1;
                // get the absolute position in this spiral
                const relative_position = this.spiral_order[j];
                const absolute_x = relative_position.x + start_pos.x;
                const absolute_y = relative_position.y + start_pos.y;
                // console.log(absolute_x, absolute_y);
                // make sure it is in canvas
                if (absolute_x < this.width && absolute_y < this.height) {
                    const smallest = picked_text_info[picked_text_info.length - 1];
                    // make sure it can fit in the smallest one
                    if (vertical_rate != -1) {
                        if (vertical) {
                            if (negative_rotation) {
                                fit_in = absolute_x - smallest.height >= 0 && absolute_y + smallest.width < this.height;
                            } else {
                                fit_in = absolute_x + smallest.height < this.width && absolute_y - smallest.width >= 0;
                            }
                        } else {
                            fit_in = absolute_x + smallest.width < this.width && absolute_y + smallest.height < this.height;
                        }
                    } else {
                        // we are just trying to find the largest font possible, no matter the orientation
                        vertical_negative_fitin = absolute_x - smallest.height >= 0 && absolute_y + smallest.width < this.height;
                        vertical_positive_fitin = absolute_x + smallest.height < this.width && absolute_y - smallest.width >= 0;
                        horizontal_fitin = absolute_x + smallest.width < this.width && absolute_y + smallest.height < this.height;
                        fit_in = vertical_negative_fitin || vertical_positive_fitin || horizontal_fitin;
                    }
                    if (fit_in) {
                        // do binary search for the fittest font size
                        if (vertical_rate != -1) {
                            ans = this.binarySearchFont(vertical, negative_rotation, picked_text_info, absolute_x, absolute_y);
                        } else {
                            var ans_horizontal = this.binarySearchFont(false, false, picked_text_info, absolute_x, absolute_y);
                            var ans_vertical_negative = this.binarySearchFont(true, true, picked_text_info, absolute_x, absolute_y);
                            var ans_vertical_positive = this.binarySearchFont(true, false, picked_text_info, absolute_x, absolute_y);
                            if (ans_horizontal > ans_vertical_negative && ans_horizontal > ans_vertical_positive) {
                                vertical = false;
                                ans = ans_horizontal
                            } else if (ans_vertical_negative > ans_horizontal && ans_vertical_negative > ans_vertical_positive) {
                                vertical = true;
                                negative_rotation = true;
                                ans = ans_vertical_negative;
                            } else {
                                vertical = true;
                                negative_rotation = false;
                                ans = ans_vertical_positive;
                            }
                        }
                        // if we have picked a word
                        if (ans != -1) {
                            var r = 0;
                            var g = 0;
                            var b = 0;
                            // we will need to set the corresponding positions to unavailable
                            const relative_pos = picked_text_info[ans].relative_pos;
                            if (picked_text_info[ans].font_size > 10) {
                                for (var k = 0; k < relative_pos.length; k++) {
                                    if (vertical) {
                                        if (negative_rotation) {
                                            x = absolute_x - relative_pos[k].y;
                                            y = absolute_y + relative_pos[k].x;
                                        } else {
                                            x = absolute_x + relative_pos[k].y;
                                            y = absolute_y - relative_pos[k].x;
                                        }
                                    } else {
                                        x = absolute_x + relative_pos[k].x;
                                        y = absolute_y + relative_pos[k].y;
                                    }
                                    const index = y * this.width + x;
                                    this.available_position[index] = false;
                                    r += this.raw_image_data.data[index * 4];
                                    g += this.raw_image_data.data[index * 4 + 1];
                                    b += this.raw_image_data.data[index * 4 + 2];
                                }
                                this.filled += relative_pos.length;
                                r /= relative_pos.length;
                                g /= relative_pos.length;
                                b /= relative_pos.length;
                            } else {
                                // if the font size is small enough, there's probably nowhere to
                                // put other words, so just consider the entire word box occupied
                                for (var m = 0; m < picked_text_info[ans].height; m++) {
                                    for (var n = 0; n < picked_text_info[ans].width; n++) {
                                        if (vertical) {
                                            if (negative_rotation) {
                                                x = absolute_x - m;
                                                y = absolute_y + n;
                                            } else {
                                                x = absolute_x + m;
                                                y = absolute_y - n;
                                            }
                                        } else {
                                            x = absolute_x + n;
                                            y = absolute_y + m;
                                        }
                                        const index = y * this.width + x;
                                        this.available_position[index] = false;
                                        r += this.raw_image_data.data[index * 4];
                                        g += this.raw_image_data.data[index * 4 + 1];
                                        b += this.raw_image_data.data[index * 4 + 2];
                                    }
                                }
                                this.filled += picked_text_info[ans].height * picked_text_info[ans].width;
                                r /= picked_text_info[ans].height * picked_text_info[ans].width;
                                g /= picked_text_info[ans].height * picked_text_info[ans].width;
                                b /= picked_text_info[ans].height * picked_text_info[ans].width;
                            }
                            // fill in that word with the corresponding color
                            ctx.fillStyle = 'rgb(' + Math.floor(r) + ', ' + Math.floor(g) + ', ' + Math.floor(b) + ')';
                            ctx.font = picked_text_info[ans].font_size + 'px "Recursive", sans-serif';
                            ctx.textBaseline = "top";
                            if (vertical) {
                                ctx.save();
                                ctx.translate(absolute_x, absolute_y);
                                if (negative_rotation) {
                                    ctx.rotate(Math.PI / 2);
                                } else {
                                    ctx.rotate(-Math.PI / 2);
                                }
                                ctx.fillText(picked_text_info[ans].txt, 0, 0);
                                ctx.restore();
                            } else {
                                ctx.fillText(picked_text_info[ans].txt, absolute_x, absolute_y);
                            }
                            return (this.filled - this.occupied) / (this.width * this.height - this.occupied);
                        }
                    }
                }
            }
        }
        // always return how much we have filled
        return (this.filled - this.occupied) / (this.width * this.height - this.occupied);
    }

    // when the words are picked, save the current canvas
    // so that user can switch between seeing the word cloud
    // and the original picture
    saveWC() {
        this.wc_image_data = this.canvas.getContext("2d").getImageData(0, 0, this.width, this.height);
    }

    // show the image cloud if it is already generated
    showWC() {
        this.canvas.getContext("2d").putImageData(this.wc_image_data, 0, 0);
    }

    // download the word cloud
    // if without background, just set rgba to 0, 0, 0, 0
    downloadWordCloud(bg_color) {
        var new_canvas = document.createElement('canvas');
        new_canvas.width = this.width;
        new_canvas.height = this.height;
        var ctx = new_canvas.getContext('2d');
        ctx.putImageData(this.wc_image_data, 0, 0);
        ctx.globalCompositeOperation = "destination-over";
        ctx.fillStyle = bg_color;
        ctx.fillRect(0, 0, this.width, this.height);
        new_canvas.toBlob(function (blob) {
            saveAs(blob, "wordcloud.png");
        });
    }
}

module.exports = wc_generator;