import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import WordCloudCanvas from "../views/WordCloudCanvas.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/Canvas",
    name: "WordCloudCanvas",
    component: WordCloudCanvas
  },
  {
    path: '/github',
    beforeEnter() { location.href = 'https://github.com/prettywordcloud/prettywordcloud.github.io' }
  }
];

const router = new VueRouter({
  routes
});

export default router;
